.container {
  display: flex;
  justify-content: center;
  padding: 1rem;
  background-color: #FFF;
  height: 90vh;
}

.map {
  max-width: 100%;
  height: auto;
}

.state-label {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 1;
  background-color: #FFF;
  padding: 20px;
  border-radius: 4px;
  font-size: 40px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
}

.state-label::before {
  content: "State: ";
}

.county-label {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 1;
  background-color: #FFF;
  padding: 20px;
  border-radius: 4px;
  font-size: 40px;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.1);
}

.county-label::before {
  content: "County: ";
}